import React, { useEffect, useState } from "react";
import { PenTool } from "react-feather";
import "react-toastify/dist/ReactToastify.css";
import { ToastContainer, toast } from "react-toastify";

import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Form,
  FormGroup,
  Input,
  Label,
  Table,
} from "reactstrap";
import Moment from "moment";
import { API_URL } from "../../constant";
import axios from "axios";
import SearchBar from "../../layout/searchbar";
const HR = (props) => {
  const [FirstName, setFirstName] = useState("");
  const [LastName, setLastName] = useState("");
  const [PhoneNumber, setPhoneNumber] = useState("");
  const [NIC, setNIC] = useState("");
  const [Civil, setCivil] = useState("Married");
  const [UserRole, setUserRole] = useState("Junior Executive");
  const [Address, setAddress] = useState("");
  const [DOB, setDOB] = useState("");
  const [Gender, setGender] = useState("");
  const [Response, setResponse] = useState("");
  const [Keyword, setKeyword] = useState("");
  const hrRouter = "hrRouter/";
  //Notification
  const notify = () => toast.success("Successfully Saved !!");
  const sendData = () => {
    let model = {
      nic: NIC,
      civilStatus: Civil,
      phoneNumber: Number(PhoneNumber),
      address: Address,
      dob: DOB,
      gender: Gender,
      fName: FirstName,
      lName: LastName,
      userRole: UserRole,
      joinDate: new Date(),
    };
    axios.post(API_URL + hrRouter + "save/", model).then((response) => {
      getData();
      notify();
    });
  };
  const getData = () => {
    axios.get(API_URL + hrRouter + "get").then((response) => {
      setResponse(response.data);
    });
  };
  useEffect(() => {
    if (!Response) {
      getData();
    }
  });
  return (
    <div>
      <br></br>
      <br></br>
      <SearchBar
        Keyword={Keyword}
        setKeyword={setKeyword}
        Response={Response}
        setResponse={setResponse}
        Router={hrRouter}
      ></SearchBar>
      <br></br>
      <div className="row">
        <div className="col-md-3"></div>
        <div className="col-md-6">
          <Card>
            <CardHeader>Create Employee Details</CardHeader>
            <CardBody>
              <Form>
                <div className="row">
                  <div className="col-md-6">
                    <FormGroup>
                      <Label>First Name</Label>
                      <Input
                        type="text"
                        placeholder="First Name"
                        onChange={(e) => {
                          setFirstName(e.target.value);
                        }}
                      />
                    </FormGroup>
                  </div>
                  <div className="col-md-6">
                    <FormGroup>
                      <Label>Last Name</Label>
                      <Input
                        type="text"
                        placeholder="Last Name"
                        onChange={(e) => {
                          setLastName(e.target.value);
                        }}
                      />
                    </FormGroup>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-6">
                    <FormGroup>
                      <Label>NIC</Label>
                      <Input
                        type="text"
                        placeholder="NIC"
                        onChange={(e) => {
                          setNIC(e.target.value);
                        }}
                      />
                    </FormGroup>
                  </div>
                  <div className="col-md-6">
                    <FormGroup>
                      <Label>Phone Number</Label>
                      <Input
                        type="text"
                        placeholder="Phone Number"
                        onChange={(e) => {
                          setPhoneNumber(e.target.value);
                        }}
                      />
                    </FormGroup>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-6">
                    <FormGroup>
                      <Label>Civil Status</Label>
                      <select
                        className="form-control"
                        onChange={(e) => {
                          setCivil(e.target.value);
                        }}
                      >
                        <option value="Married">Married</option>
                        <option value="Single">Single</option>
                      </select>
                    </FormGroup>
                  </div>
                  <div className="col-md-6">
                    <FormGroup>
                      <Label>User Role</Label>
                      <select
                        className="form-control"
                        onChange={(e) => {
                          setUserRole(e.target.value);
                        }}
                      >
                        <option value="Junior Executive">
                          Junior Executive
                        </option>
                        <option value="Executive">Executive</option>
                        <option value="Senior Executive">
                          Senior Executive
                        </option>
                        <option value="Associate Manager">
                          Associate Manager
                        </option>
                        <option value="Manager">Manager</option>
                        <option value="Genaral Manager">Genaral Manager</option>
                        <option value="Director">Director</option>
                      </select>
                    </FormGroup>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-6">
                    <FormGroup>
                      <Label>Address</Label>
                      <Input
                        type="text"
                        placeholder="Address"
                        onChange={(e) => {
                          setAddress(e.target.value);
                        }}
                      />
                    </FormGroup>
                  </div>
                  <div className="col-md-6">
                    <FormGroup>
                      <Label>Date of Birth</Label>
                      <Input
                        type="date"
                        placeholder="NIC"
                        onChange={(e) => {
                          setDOB(Moment().format(e.target.value));
                        }}
                      />
                    </FormGroup>
                  </div>
                </div>
                <FormGroup>
                  <Label>Gender</Label>
                  <select
                    className="form-control"
                    onChange={(e) => {
                      setGender(e.target.value);
                    }}
                  >
                    <option value="Male">Male</option>
                    <option value="Female">Female</option>
                  </select>
                </FormGroup>
              </Form>
              <br></br>
              <div className="row">
                <div className="col-md-4"></div>
                <div className="col-md-6">
                  <Button
                    color="primary"
                    onClick={() => {
                      sendData();
                    }}
                  >
                    Create Employee Details
                  </Button>
                </div>
              </div>
            </CardBody>
          </Card>
        </div>
        <div className="col-md-3"></div>
      </div>
      <br></br>
      <br></br>
      <div className="row">
        <div className="col-md-1"></div>
        <div className="col-md-10">
          <Card>
            <CardHeader>All Employee Details</CardHeader>
            <CardBody>
              <Table>
                <tr>
                  <th>Record Id</th>
                  <th>First Name</th>
                  <th>Last Name</th>
                  <th>NIC</th>
                  <th>Phone Number</th>
                  <th>Civil Status</th>
                  <th>User Role</th>
                  <th>Address</th>
                  <th>Gender</th>
                  <th>Date of Birth</th>
                  <th>Date of Joined</th>
                  <th>Actions</th>
                </tr>
                {Response &&
                  Response.map((item, index) => {
                    return (
                      <tr>
                        <td>{Number(index) + 1}</td>
                        <td>{item.fName}</td>
                        <td>{item.lName}</td>
                        <td>{item.nic}</td>
                        <td>{item.phoneNumber}</td>
                        <td>{item.civilStatus}</td>
                        <td>{item.address}</td>
                        <td>{item.userRole}</td>
                        <td>{item.gender}</td>
                        <td>{Moment(item.dob).format("yyyy-DD-MM")}</td>
                        <td>{Moment(item.joinDate).format("yyyy-DD-MM")}</td>

                        <td>
                          <div className="row">
                            <div className="col-md-5">
                              <Button type="">Delete</Button>
                            </div>
                            <div className="col-md-2"></div>
                            <div className="col-md-5">
                              <Button color="primary">Edit</Button>
                            </div>
                          </div>
                        </td>
                      </tr>
                    );
                  })}
              </Table>
            </CardBody>
          </Card>
        </div>
        <div className="col-md-1"></div>
      </div>
    </div>
  );
};
export default HR;
